import * as Yup from 'yup';
import { useEffect, useState, } from 'react';
import { useSnackbar } from 'notistack';
// swal
import swal from 'sweetalert';
import { useNavigate } from 'react-router-dom';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Alert, } from '@mui/material';
import axios from '../../../../utils/axios';
import { FormProvider, RHFSelect, RHFTextField, } from '../../../../components/hook-form';
// format number 
import { fCurrency, } from '../../../../utils/formatNumber';
import useAuth from '../../../../hooks/useAuth';
import { PATH_DASHBOARD } from '../../../../routes/paths';

// ----------------------------------------------------------------------

export default function PurchaseRechargeCard() {
  const NewUserSchema = Yup.object().shape({
    card_name: Yup.string().required('Card Name Required'),
    network: Yup.string().required(),
    plan_type: Yup.string().required(),
    quantity: Yup.string().min(1, 'Mimimum Quantity is 1').max(100, 'Maximum Quantity is 100').required('Quantity Required'),
  });
  const pinSchema = Yup.object().shape({
    network: Yup.string().required(),
    plan_type: Yup.string().required(),
    card_name: Yup.string().required('Card Name Required'),
    pin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),
    quantity: Yup.string().min(1, 'Mimimum Quantity is 1').max(100, 'Maximum Quantity is 100').required('Quantity Required'),
  });
  const { enqueueSnackbar } = useSnackbar();
  const [adexnetwork, setNetwork] = useState();
  const navigate = useNavigate();
  const [price, SetPrice] = useState(undefined);
  const [type, PlanType] = useState([]);
  const [amount, SetAmount] = useState();
  const AccessToken = window.localStorage.getItem('accessToken');
  const { setting } = useAuth();
  const pin = setting?.setting?.allow_pin === 1;
  const methods = useForm({
    resolver: yupResolver(pin ? pinSchema : NewUserSchema),
  });
  const [discount, SetDiscount] = useState();
  const {
    setValue,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;


  // get network over here
  useEffect(() => {
    axios.get('/api/website/app/network').then(data => {
      setNetwork(data.data?.network);
    });
  }, []);



  const changeme = async (me) => {
    setValue('network', me)
    try {
      const resp = await axios.post(`/api/get/recharge_card_plans/${AccessToken}/adex`, {
        network: me
      });
      PlanType(resp?.data?.plan);
    } catch (_) {
      (PlanType([]));
    }
  }
  const onSubmit = async (data) => {
    try {
      const isConfirmed = await swal({
        title: 'Confirmation',
        text: `You are purchasing ${data.quantity} recharge cards for a total of ${amount}.`,
        icon: 'warning',
        buttons: ['Cancel', 'Confirm'],
      });
      if (isConfirmed) {
        const resp = await axios.post('/api/recharge_card', {
          network: data.network,
          plan_type: data.plan_type,
          pin: data.pin,
          token: AccessToken,
          card_name: data.card_name,
          quantity: data.quantity
        })
        if (resp?.data?.status === 'success') {
          swal({
            title: 'success', text: `${resp?.data?.message}`, icon: 'success', buttons: [
              'Okay',
              'Print'
            ],
            dangerMode: true,
          }).then((isConfirm) => {
            if (isConfirm) {
              navigate(`${PATH_DASHBOARD.general.invioce}/${resp?.data?.transid}/success_recharge_card`, { replace: true });
            }
          });
        } else if (resp?.data?.status === 'fail') {
          swal('Opps', `${resp?.data?.message}`, 'error');
        } else {
          swal('Info', `${resp?.data?.message}`, 'info');
        }
      }
    } catch (error) {
      if (error?.data?.message !== undefined) {
        swal('Opps', `${error?.data?.message}`, 'error');
      } else if (error?.message !== undefined) {
        swal('Opps', `${error?.message}`, 'error');
      } else {
        swal('Opps', 'Something went wrong', 'error');
      }
    }
  };

  const GetDataCode = async (me) => {
    setValue('plan_type', me.target.value)
    const value = type.filter((item) => item.plan_id === me.target.value)
    if (value[0]) {
      SetPrice(value[0].amount);
    }
  }
  const Calculate = async (data) => {
    SetAmount(data.target.value);
    const quantity = data.target.value;
    setValue('quantity', quantity)
    if (quantity) {
      if (quantity > 0) {
        if (quantity <= 100) {
          if (price !== undefined) {
            SetDiscount(`₦${fCurrency(price * quantity)}`)
          } else {
            enqueueSnackbar('select plan type', { variant: 'error' })
          }
        } else {
          SetDiscount('Maximum Quantity is 100')
        }
      } else {
        SetDiscount('Minimum Qunatity is 1');
      }
    } else {
      SetDiscount(undefined)
    }
  }



  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFSelect name="network" label="Network" placeholder="Network" onChange={(e) => changeme(e.target.value)}>
                <option value="" />
                {adexnetwork?.map((option) => (
                  <option key={option?.code} value={option?.plan_id} disabled={option?.recharge_card === 0}>
                    {option?.network}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="plan_type" label="Plan Type" onChange={GetDataCode}>
                <option value="" />
                {type?.map((option) => (
                  <option key={option?.plan_id} value={option?.plan_id}>
                    {option?.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField name="quantity" label="Quantity" type="number" value={amount} inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} onChange={Calculate} />
              {pin && <RHFTextField name="pin" label="Transaction Pin" type="password" autoComplete="new-password" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />}

              {discount !== undefined && <RHFTextField name="Amount" label="Amount" value={discount} disabled />}
              <RHFTextField name="card_name" label="Card Name" />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Purchase
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
